var _this;
//登录的用户的增加用户入库的api
import {register} from '../../static/utils/api.js';

//封装好的登录的函数
const login = require('../../static/utils/utils.js').Login;
const Login = new login();

export default {
	data() {
		return {
			canIUse:wx.canIUse('button.open-type.getUserInfo')
		}
	},
	
	//登录之前就查看本地是否存在用户证明授权登录过直接跳转到申请表
	mounted() {
	   
	},
	 methods:{
	  bindGetUserInfo:function(){
		  let openId;
		  let sessionKey;
		  uni.showLoading({
			  title:"登录中",
		  	  success:()=>{
			   Login.getCode()
			   .then(data=>{
			   		return Login.getOpenId(data)
			   })
			   .then(data=>{
			   	   openId = data.data.openid;
				   sessionKey = data.data.session_key;
			   	   return Login.getUserInfo()
			   })
			   .then(data=>{
			   	   let userInfo = {
					    ...data.userInfo,
			   			openId,
						sessionKey
			   		};
					//console.log("发送到服务器的信息",userInfo)  
			   	    return Login.insertUser(userInfo,register,"POST")
			   })
			   .then(data=>{
				   uni.hideLoading();
				   console.log("返回的用户的信息",data.data)
				  // console.log(userInfo)
				   uni.setStorage({
				   	 key:'userInfo',
					 data:data.data,
					 success:()=>{
						 uni.redirectTo({
						 	url:'../apply/apply'
						 })
					 },
					 fail:()=> {
					   	uni.showToast({
					   		title:"网络连接错误",
							duration:1000,
							icon:"loading"
					   	})
		              }
				    })		
				})	     
			   .catch(error=>{
			   	  console.log(error)
			   })	
			}
		  })
	}
  }
}
